body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo {
  height: 70px;
  width: 70px;
}
.clsdlogo {
  height: 150px;
  width: 150px;
}
.banner {
  height: auto%;
  width: 75%;
}


.footerlogo {
  height: 70px;
  width: 70px;
}


.logo1 {
  height: 400px;
  width: 400px;
}



.clsd {
  height: 400px;
  width: 800;
}



.footerlogo {
  height: 70px;
  width: 70px;
}

.subfooterlogo {
  height: 120px;
  width:120px;
}

.paragraph{
 margin:10px;
 overflow: hidden;
 text-overflow: ellipsis;
 display: -webkit-box;
 -webkit-line-clamp: 1;
 -webkit-box-orient: vertical;
}


.pagebreak{  page-break-after: always !important; }

.posts{
    flex: 9 1;
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
}

.post {
  width: 385px;
  margin: 0px 25px 40px 25px;
  display: flex;
  flex-direction: column;
}

.postImg {
  width: 385px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  cursor: pointer;
}

.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #999999;
  margin-top: 15px;
}

.postDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}



.singlePost {
  flex: 9 1;
}

.singlePostWrapper {
  padding: 20px;
  padding-right: 0;
}

.singlePostImg {
  width: 100%;
  height: 500;
  border-radius: 8px;
  object-fit: cover;
  margin-top: 20px;
}

.singlePostTitle {
  text-align: center;
  margin: 10px;
  font-size: 40px;
  font-family: "Lora", sans-serif;
}

.singlePostEdit {
  float: right;
  font-size: 16px;
}

.singlePostIcon {
  margin-left: 10px;
  cursor: pointer;
}

.singlePostIcon:first-child {
  color: teal;
}
.singlePostIcon:last-child {
  color: tomato;
}

.singlePostInfo {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: #be9656;
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
}

.singlePostAuthor{
  margin-left: 5px;
}

.singlePostDesc {
  color: #666;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDesc::first-letter {
  margin-left: 20px;
  font-size: 25px;
  font-weight: 400;
}
.singlePostParagraph{
  color: #666;
  font-size: 18px;
  line-height: 30px;
}
.singlePostParagraph::first-letter {
  margin-left: 30px;
  font-size: 18px;

}


.singlePostImage {
  width: 100%;
  height: 500;
  border-radius: 8px;
  object-fit: cover;
  margin-top: 20px;
}

.header {
  background: url("https://firebasestorage.googleapis.com/v0/b/sparrow-9f907.appspot.com/o/mediaImages%2Fbg3-clsd.jpg?alt=media&token=02f3c352-b517-4d11-afcd-44ea74721c11")
  no-repeat center center/cover;
  height: 35vh;
  position: relative;

{/*color: white;*/}
}

.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
}


